var url = 'http://fwh.bhgmall.com.cn';
var apiPrefix = url + '/wechat/api';

var config = {
  projectname: "hlgzh",
  urls:'http://fwh.bhgmall.com.cn',
  api: {
    /**
     * :
     */
    getbanner: '/indexBanner/getbanner',
    /**
     * 
     */
    bannerId:'/indexBanner/getBannerDetail',

    /**
     * 
     */
    bannerDetails:'/interview/getLogDetail',
    /**
     * 
     * :
     */
    getMdlist:'/mdinfo/getmdlist',
    /**
     * 
     */
    getmdInfo:'/mdinfo/getmdinfo',
    /**
     * banner: POST
     */
    paperGetbanner:'/ebook/getbanner',
    /**
     * log: POST
     */
    paperGetlog:'/ebook/getlog',
    /**
     * banner:
     */
    interviewGetbanner:'/interview/getbanner',
    /**
     * log:
     */
    interviewGetlog:'/interview/getlog',
    /**
     * app:
     */
    getapplist:'/applink/getapplist',
     /**
     * 
     */
    getinfo:'/kdlx/getinfo',
    /**
     * 
     */
    contactgetinfo:'/lxwm/getinfo',
     /**
     * 
     */
    opegetinfo:'/rcyy/getinfo'
  },
};

for (var key in config.api) {
  config.api[key] = apiPrefix + config.api[key];
}

module.exports = config;


